                });

                map.on('mousemove', 'airport', function(e) {
                    map.getCanvas().style.cursor = 'pointer';

                    let feature = e.features[0];
                    popup.setLngLat(feature.geometry.coordinates)
                        .setText(feature.properties.name + ' (' + feature.properties.abbrev + ')')
                        .addTo(map);
                });

                map.on('mouseleave', 'airport', function() {
                    map.getCanvas().style.cursor = '';
                    popup.remove();
                });




                function getUniqueFeatures(array, comparatorProperty) {
                    var existingFeatureKeys = {};
                    var uniqueFeatures = array.filter(function(el) {
                        if (existingFeatureKeys[el.properties[comparatorProperty]]) {
                            return false;
                        } else {
                            existingFeatureKeys[el.properties[comparatorProperty]] = true;
                            return true;
                        }
                    });

                    return uniqueFeatures;
                }


                map.on('moveend', () => {
                    // 현재화면에 Returns an array of GeoJSON Feature objects
                    let features = map.queryRenderedFeatures({layers:['airport']});

                    if (features) {
                        // GeoJSON에서 선택된  value의  정보만 가져옴
                        let uniqueFeatures = getUniqueFeatures(features, "GMP");

                        renderListings(uniqueFeatures);
                    }
                });



                map.addControl(new mapboxgl.GeolocateControl({
